import { Component, OnInit } from '@angular/core';

type Example = {
  videoId: string,
  assignmentId: string,
  objectId: string,
  frameId: string,
  icon: string,
  height: number,
  width: number,
}

@Component({
  selector: 'modification-helper',
  templateUrl: './modification-helper.component.html',
  styleUrls: ['./modification-helper.component.css']
})
export class ModificationHelperComponent implements OnInit {

  examples: Example[] = [];

  constructor() {
   for (var i=0; i<10; i++){
       this.examples.push({
           videoId: 'ILSVRC2015_val_00044010',
           assignmentId: '',
           frameId: '00'+(i*4+10),
           icon: i%2 ? 'thumbdown' : 'thumbup',
           objectId: 'obj0',
           height: 337,
           width: 600
       });
   }
  }

  ngOnInit() {
  }

}



