import { Component, Input, AfterViewInit, ViewChild, ElementRef, HostBinding } from '@angular/core'
import { Http, Response } from '@angular/http'
import { ConfigService } from '../config.service'
import * as _ from 'lodash'
import { Bound } from './bound'

@Component({
  selector: 'base-image',
  templateUrl: './base-image.component.html',
  /* viewProviders: [HTTP_PROVIDERS], */
  styleUrls: ['./base-image.component.css'],
})
export class BaseImageComponent implements AfterViewInit {

  config: ConfigService;
  http: Http;
  ctx: CanvasRenderingContext2D;


  @ViewChild("canvas") canvasRef: ElementRef;
  @Input() height: number;
  @Input() width: number;
  @Input() videoId: string;
  @Input() objectId: string;
  @Input() frameId: string;
  @Input() assignmentId: string = "";
  @Input() icon: string = "";

  get svgsrc() {
    let name = window.location.pathname.replace('index.html', '');
    if (this.icon == "") return "";
    return `${name}assets/svgs/${this.icon}.svg`;
  }
  get drawsrc() {
    let name = window.location.pathname.replace('index.html', '');
    if (this.assignmentId){
        return `${name}experimentdata/${this.videoId}-${this.objectId}-${this.frameId}-${this.assignmentId}`
    }else{
        return "";
    }
  }


  constructor(config: ConfigService, http: Http) {
    this.config = config;
    this.http = http;
    this.videoId = this.videoId || config.videoId;
    this.objectId = this.objectId || config.objectId;
    this.frameId = this.frameId || config.frameId;
    this.config.size.subscribe((size) => {
      const [width, height] = size;
      this.load(width, height);
    })
  }

  ngAfterViewInit() {
    this.ctx = this.canvasRef.nativeElement.getContext('2d');
    this.load(this.width, this.height);
  }

  load(width: number, height: number) {
    const img = <HTMLImageElement>document.createElement('img');
    this.height = this.height || height;
    this.width = this.width || width;
    img.src = this.config.getFrameSrc(this.videoId, this.objectId, this.frameId);
    img.height = this.height;
    img.width = this.width;
    img.onload = () => {
      this.ctx.drawImage(img, 0, 0, width, height);
      this.http.get(this.config.getBoundingSrc(this.videoId, this.frameId))
        .subscribe((res) => {
          const objectId = this.objectId.replace('obj', '');
          const bound = Bound.parseXML(res.text(), width, height, objectId);
          bound.draw(this.ctx);
        });
    }
  }
}



