import { Component, OnInit } from '@angular/core';

type Example = {
  videoId: string,
  objectId: string,
  frameId: string,
  icon: string,
  height: number,
  width: number,
}

@Component({
  selector: 'verification-helper',
  templateUrl: './verification-helper.component.html',
  styleUrls: ['./verification-helper.component.css']
})
export class VerificationHelperComponent implements OnInit {

  examples: Example[] = [];
  constructor() {
    for (var i = 0; i < 10; i++) {
      this.examples.push({
        videoId: 'ILSVRC2015_val_00044010',
        frameId: '000' + i,
        icon: i%2 ? 'thumbdown' : 'thumbup',
        objectId: 'obj0',
        height: 337,
        width: 600
      });
    }
  }
    ngOnInit() {
    }

  }



